import React from 'react'
import dom1 from '../assets/img/do1.jpg';
import dom2 from '../assets/img/do2.jpg';
import dom3 from '../assets/img/do3.png';
import dom4 from '../assets/img/do4.jpg';
import dom5 from '../assets/img/do5.png';
import dom6 from '../assets/img/do6.jpg';

import { Container } from 'react-bootstrap';

export const Pagcuat = () => {
  return (
    <Container>
    <div className='hogar'>
      <center><h2>Electrodomesticos</h2></center>
      <div className="card" style={{width: '18rem'}}>
          <img src={dom1} class="card-img-top" />
          <div className="card-body">
          <p className="card-text">
          <h5>Televisor Hisense</h5>
            Es un aparato electrónico destinado a la recepción y reproducción de señales de televisión. Usualmente consta de una pantalla y mandos o controles. </p>
          </div>
      </div>
      <div className="card" style={{width: '18rem'}}>
          <img src={dom2} class="card-img-top" />
          <div className="card-body">
          <p className="card-text">
          <h5>Lavadora-Secadora</h5>
          Es un dispositivo electromecánico que dispone de un tambor adonde se deposita la ropa sucia. Dicho tambor, a su vez, cuenta con orificios que permiten el ingreso de agua. </p>
          </div>
      </div>
      <div className="card" style={{width: '18rem'}}>
          <img src={dom3} class="card-img-top" />
          <div className="card-body">
          <p className="card-text">
          <h5>Microondas Samsung</h5>
          Un horno de microondas es un electrodoméstico destinado a cocinar o calentar alimentos que actúa calentando el agua que contienen o los líquidos que se introducen en él.</p>
          </div>
      </div>
      <div className="card" style={{width: '18rem'}}>
          <img src={dom4} class="card-img-top" />
          <div className="card-body">
          <p className="card-text">
          <h5>Aspiradora</h5>
          Las aspiradoras son máquinas móviles, domésticas o industriales, para la limpieza de suelos y superficies similares, que arrastran el polvo y otras partículas a un recipiente</p>
          </div>
      </div>
      <div className="card" style={{width: '18rem'}}>
          <img src={dom5} class="card-img-top" />
          <div className="card-body">
          <p className="card-text">
          <h5>Plancha de ropa</h5>
          Utensilio de forma triangular, con base metálica lisa y pulida y un asa en la parte superior, que se calienta normalmente con energía eléctrica y sirve para planchar.</p>
          </div>
      </div>
      <div className="card" style={{width: '18rem'}}>
          <img src={dom6} class="card-img-top" />
          <div className="card-body">
          <p className="card-text">
          <h5>Aire acondicionado</h5>
          Estos equipos utilizan sistemas de refrigeración para conseguir un cambio de temperatura.extraer calor del foco a enfriar, usando una máquina frigorífica convencional.</p>
          </div>
      </div>
    </div>
    
    </Container>
  )
}
